import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    getPostulantesConvocatoria(ctx, queryParams) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-social/beca/convocatoria/2/postulante', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // fetchUser(ctx, { id }) {
    //   return new Promise((resolve, reject) => {
    //     axios
    //       .get(`/social/convocatorias/${id}`)
    //       .then(response => resolve(response))
    //       .catch(error => reject(error))
    //   })
    // },
    getConvocatoria(ctx, idConvocatoria) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-social/beca/convocatoria/${idConvocatoria}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
